"use client"

import React, { useState, useEffect } from "react"
import { View, Text, Button, StyleSheet, Dimensions, ScrollView, SafeAreaView } from "react-native"
import Slider from "@react-native-community/slider"
import { LineChart } from "react-native-chart-kit"
import { Svg, Rect, Circle, Line, Text as SvgText, Polygon } from "react-native-svg"
import TheoreticalGuide from "./TheoreticalGuide"

interface Bubble {
  id: number
  x: number
  y: number
  size: number
  opacity: number
  type: "hydrogen" | "oxygen"
}

interface Ion {
  id: number
  x: number
  y: number
  type: "H+" | "OH⁻"
  vx: number
  vy: number
}

interface ExperimentationScreenProps {
  onBack: () => void
}

const ExperimentationScreen: React.FC<ExperimentationScreenProps> = ({ onBack }) => {
  const [voltage, setVoltage] = useState<number>(5)
  const [current, setCurrent] = useState<number>(0.5)
  const [bubbles, setBubbles] = useState<Bubble[]>([])
  const [ions, setIons] = useState<Ion[]>([])
  const [isRunning, setIsRunning] = useState<boolean>(false)
  const [showGuide, setShowGuide] = useState<boolean>(false)

  // Data tracking for the graph.
  const [time, setTime] = useState<number>(0)
  const [hydrogenData, setHydrogenData] = useState<number[]>([])
  const [oxygenData, setOxygenData] = useState<number[]>([])

  const productionFactor = 10
  const faraday = 96485
  const gasConstant = 0.0821 // L·atm/(mol·K)
  const temperature = 298 // K (25°C)
  const pressure = 1 // atm

  // Initialize ions
  useEffect(() => {
    const initialIons: Ion[] = []
    const ionTypes: Ion["type"][] = ["H+", "OH⁻"]

    for (let i = 0; i < 15; i++) {
      initialIons.push({
        id: i,
        x: 100 + Math.random() * 200,
        y: 150 + Math.random() * 100,
        type: ionTypes[Math.floor(Math.random() * ionTypes.length)],
        vx: (Math.random() - 0.5) * 0.5,
        vy: (Math.random() - 0.5) * 0.5,
      })
    }
    setIons(initialIons)
  }, [])

  // Calculate gas production rate in mL/s
  const calculateGasRate = (current: number, moles: number) => {
    const volume = (moles * gasConstant * temperature) / pressure
    return volume * 1000 // Convert to mL
  }

  // Animation loop for electrolysis simulation
  useEffect(() => {
    if (!isRunning) return

    const interval = setInterval(() => {
      // Update bubbles
      setBubbles((prev) => {
        const updated = prev
          .map((bubble) => ({
            ...bubble,
            y: bubble.y - 1,
            opacity: bubble.opacity - 0.02,
          }))
          .filter((bubble) => bubble.opacity > 0)

        // Add new hydrogen bubbles
        if (Math.random() < (0.1 * voltage) / 10) {
          updated.push({
            id: Date.now() + Math.random(),
            x: 80 + Math.random() * 20,
            y: 140,
            size: 3 + Math.random() * 4,
            opacity: 1,
            type: "hydrogen",
          })
        }

        // Add new oxygen bubbles
        if (Math.random() < (0.07 * voltage) / 10) {
          updated.push({
            id: Date.now() + Math.random(),
            x: 320 + Math.random() * 20,
            y: 140,
            size: 2 + Math.random() * 3,
            opacity: 1,
            type: "oxygen",
          })
        }

        return updated
      })

      // Update ion movement
      setIons((prev) =>
        prev.map((ion) => {
          let newVx = ion.vx
          let newVy = ion.vy

          // Ion migration towards electrodes
          if (isRunning) {
            if (ion.type === "H+") {
              newVx += (90 - ion.x) * 0.0001 * (voltage / 5) // Move towards cathode
            } else {
              newVx += (310 - ion.x) * 0.0001 * (voltage / 5) // Move towards anode
            }
          }

          const newX = ion.x + newVx
          const newY = ion.y + newVy

          // Boundary conditions
          if (newX < 100 || newX > 300) newVx *= 0
          if (newY < 150 || newY > 250) newVy *= 0

          return {
            ...ion,
            x: Math.max(100, Math.min(300, newX)),
            y: Math.max(150, Math.min(250, newY)),
            vx: newVx,
            vy: newVy,
          }
        }),
      )
    }, 50)

    return () => clearInterval(interval)
  }, [isRunning, voltage])
  
  useEffect(() => {
    if (voltage > 1 && current > 0.1) {
      setIsRunning(true)
    } else {
      setIsRunning(false)
    }
  }, [voltage, current])
  
  useEffect(() => {
    const trackingInterval = setInterval(() => {
      if (voltage > 1 && current > 0.1) {
        const hydrogenRate = calculateGasRate(current, current / (2 * faraday))
        const oxygenRate = calculateGasRate(current, current / (4 * faraday))
        setHydrogenData((prev) => [...prev, (prev.slice(-1)[0] || 0) + hydrogenRate])
        setOxygenData((prev) => [...prev, (prev.slice(-1)[0] || 0) + oxygenRate])
        setTime((prev) => prev + 1)
      }
    }, 1000)
    return () => clearInterval(trackingInterval)
  }, [voltage, current])

  const chartLabels = Array.from({ length: Math.max(10, time) }, (_, i) => (i + 1).toString()).slice(-10)
  const chartHydrogenData = hydrogenData.length 
    ? [...Array(Math.max(0, 10 - hydrogenData.length)).fill(0), ...hydrogenData].slice(-10) 
    : [0]
  const chartOxygenData = oxygenData.length 
    ? [...Array(Math.max(0, 10 - oxygenData.length)).fill(0), ...oxygenData].slice(-10) 
    : [0]

  const totalHydrogen = hydrogenData.length ? hydrogenData[hydrogenData.length - 1] : 0
  const totalOxygen = oxygenData.length ? oxygenData[oxygenData.length - 1] : 0

  return (
    <SafeAreaView style={styles.safeArea}>
      <View style={styles.mainContainer}>
        {/* LEFT PANE: Controls, Graphs, and Data */}
        <View style={styles.leftPane}>
          <ScrollView style={styles.leftScroll} contentContainerStyle={styles.leftContent}>
            <View style={styles.headerBar}>
              <Button title="Inapoi" onPress={onBack} color="#007BFF" />
              <Text style={styles.headerTitle}>Experimentare</Text>
              <Button title="GHID" onPress={() => setShowGuide(true)} color="#007BFF" />
            </View>
            <View style={styles.controls}>
              <View style={styles.sliderContainer}>
                <Text style={styles.controlLabel}>Tensiune: {voltage.toFixed(1)} V</Text>
                <Slider
                  style={styles.slider}
                  minimumValue={1}
                  maximumValue={12}
                  step={0.1}
                  value={voltage}
                  onValueChange={setVoltage}
                  minimumTrackTintColor="#007BFF"
                  maximumTrackTintColor="#ccc"
                  thumbTintColor="#009688"
                />
              </View>
              <View style={styles.sliderContainer}>
                <Text style={styles.controlLabel}>Intensitate: {current.toFixed(1)} A</Text>
                <Slider
                  style={styles.slider}
                  minimumValue={0.1}
                  maximumValue={5}
                  step={0.1}
                  value={current}
                  onValueChange={setCurrent}
                  minimumTrackTintColor="#007BFF"
                  maximumTrackTintColor="#ccc"
                  thumbTintColor="#009688"
                />
              </View>
            </View>
            <View style={styles.circuitDiagram}>
              <View style={styles.battery}>
                <Text style={styles.batteryText}>Sursa</Text>
                <Text style={styles.batteryValue}>
                  {voltage.toFixed(1)} V {current.toFixed(1)} A
                </Text>
              </View>
            </View>
            <Text style={styles.graphTitle}>Viteza de Producție a Gazelor</Text>
            <LineChart
              data={{
                labels: chartLabels,
                datasets: [
                  {
                    data: chartHydrogenData,
                    color: (opacity = 1) => `rgba(0, 123, 255, ${opacity})`,
                    strokeWidth: 2,
                  },
                  {
                    data: chartOxygenData,
                    color: (opacity = 1) => `rgba(255, 193, 7, ${opacity})`,
                    strokeWidth: 2,
                  },
                ],
                legend: ["Hidrogen", "Oxigen"],
              }}
              width={Dimensions.get("window").width * 0.55}
              height={220}
              chartConfig={{
                backgroundColor: "#E3F2FD",
                backgroundGradientFrom: "#E3F2FD",
                backgroundGradientTo: "#E3F2FD",
                decimalPlaces: 1,
                color: (opacity = 1) => `rgba(0, 123, 255, ${opacity})`,
                labelColor: (opacity = 1) => `rgba(51, 51, 51, ${opacity})`,
                style: { borderRadius: 16 },
                propsForDots: {
                  r: "3",
                  strokeWidth: "1",
                  stroke: "#007BFF",
                },
                fillShadowGradientOpacity: 0.6,
              }}
              bezier
              withInnerLines={true}
              withOuterLines={true}
              fromZero={true}
              style={{
                marginVertical: 8,
                borderRadius: 16,
                alignSelf: "center",
              }}
            />
            <Text style={styles.axisCaption}>X: Timp (s) | Y: Producție cumulativă</Text>
            <View style={styles.graphInfo}>
              <Text style={styles.infoText}>Timp de simulare: {time} s</Text>
              <Text style={styles.infoText}>
                Tensiune: {voltage.toFixed(1)} V | Intensitate: {current.toFixed(1)} A
              </Text>
              <Text style={styles.infoText}>
                Viteza de producere H₂: {calculateGasRate(current, current / (2 * faraday)).toFixed(2)} mL/s
              </Text>
              <Text style={styles.infoText}>
                Viteza de producere O₂: {calculateGasRate(current, current / (4 * faraday)).toFixed(2)} mL/s
              </Text>
            </View>
          </ScrollView>
        </View>

        {/* RIGHT PANE: Water Electrolysis Apparatus */}
        <View style={styles.rightPane}>
          <Svg width="400" height="400" viewBox="0 0 400 400" style={styles.apparatusSVG}>
            {/* Power source */}
            <Rect x="150" y="20" width="100" height="40" fill="#f4f4f4" stroke="#333" strokeWidth="2" rx="5" />
            <SvgText x="200" y="45" textAnchor="middle" fontSize="12" fontWeight="bold">
              Sursa de curent
            </SvgText>
            <SvgText x="200" y="60" textAnchor="middle" fontSize="10">
              {voltage.toFixed(1)}V
            </SvgText>

            {/* Battery terminals */}
            <SvgText x="140" y="35" textAnchor="middle" fontSize="18" fontWeight="bold">
              -
            </SvgText>
            <SvgText x="260" y="35" textAnchor="middle" fontSize="18" fontWeight="bold">
              +
            </SvgText>

            {/* Wires */}
            <Line x1="150" y1="40" x2="90" y2="40" stroke="#333" strokeWidth="3" />
            <Line x1="250" y1="40" x2="310" y2="40" stroke="#333" strokeWidth="3" />
            <Line x1="90" y1="40" x2="90" y2="100" stroke="#333" strokeWidth="3" />
            <Line x1="310" y1="40" x2="310" y2="100" stroke="#333" strokeWidth="3" />

            {/* Current flow arrows */}
            {isRunning && (
              <>
                <Polygon points="120,40 130,35 130,45" fill="#333" />
                <Polygon points="280,40 290,35 290,45" fill="#333" />
                <SvgText x="50" y="70" fontSize="12">
                  e⁻
                </SvgText>
                <Polygon points="55,75 60,70 60,80" fill="#333" />
                <SvgText x="350" y="70" fontSize="12">
                  e⁻
                </SvgText>
                <Polygon points="345,80 350,75 350,85" fill="#333" />
              </>
            )}

            {/* Container */}
            <Rect x="70" y="100" width="260" height="200" fill="none" stroke="#666" strokeWidth="3" rx="10" />

            {/* Solution */}
            <Rect x="75" y="140" width="250" height="155" fill="#e6f3ff" opacity="0.7" rx="5" />

            {/* Water molecule label */}
            <SvgText x="200" y="130" textAnchor="middle" fontSize="14" fontWeight="bold">
              H₂O
            </SvgText>

            {/* Electrodes */}
            {/* Cathode (left) */}
            <Rect x="80" y="100" width="20" height="140" fill="#c0c0c0" stroke="#333" strokeWidth="2" />
            <SvgText x="60" y="180" fontSize="12" fontWeight="bold" fill="#e91e63">
              Pt
            </SvgText>
            <SvgText x="45" y="195" fontSize="12" fontWeight="bold" fill="#e91e63">
              CATOD
            </SvgText>
            <SvgText x="55" y="210" fontSize="16" fontWeight="bold" fill="#e91e63">
              -
            </SvgText>

            {/* Anode (right) */}
            <Rect x="300" y="100" width="20" height="140" fill="#c0c0c0" stroke="#333" strokeWidth="2" />
            <SvgText x="330" y="180" fontSize="12" fontWeight="bold" fill="#2196f3">
              Pt
            </SvgText>
            <SvgText x="325" y="195" fontSize="12" fontWeight="bold" fill="#2196f3">
              ANOD
            </SvgText>
            <SvgText x="340" y="210" fontSize="16" fontWeight="bold" fill="#2196f3">
              +
            </SvgText>

            {/* Ions */}
            {ions.map((ion) => (
              <React.Fragment key={ion.id}>
                <Circle cx={ion.x} cy={ion.y} r="4" fill={ion.type === "H+" ? "#ff9800" : "#e1bee7"} opacity="0.8" />
                <SvgText x={ion.x} y={ion.y + 1} textAnchor="middle" fontSize="8" fontWeight="bold">
                  {ion.type}
                </SvgText>
              </React.Fragment>
            ))}

            {/* Gas bubbles */}
            {bubbles.map((bubble) => (
              <Circle
                key={bubble.id}
                cx={bubble.x}
                cy={bubble.y}
                r={bubble.size}
                fill={bubble.type === "hydrogen" ? "#87ceeb" : "#ffeb3b"}
                opacity={bubble.opacity}
              />
            ))}

            {/* Gas labels */}
            <SvgText x="90" y="120" textAnchor="middle" fontSize="14" fontWeight="bold">
              H₂(g)
            </SvgText>
            <SvgText x="310" y="120" textAnchor="middle" fontSize="14" fontWeight="bold">
              O₂(g)
            </SvgText>

            {/* Ion flow arrows */}
            {isRunning && (
              <>
                <polygon points="160,220 150,215 150,225" fill="#87ceeb" />
                <SvgText x="160" y="225" fontSize="12">
                  OH⁻
                </SvgText>
                <Polygon points="260,220 270,215 270,225" fill="#ff9800" />
                <SvgText x="230" y="225" fontSize="12">
                  H⁺
                </SvgText>
              </>
            )}

            {/* Chemical equations */}
            <SvgText x="120" y="340" textAnchor="middle" fontSize="10" fontWeight="bold" fill="#e91e63">
              2H₂O + 2e⁻ → H₂(g) + 2OH⁻
            </SvgText>
            <SvgText x="120" y="355" textAnchor="middle" fontSize="10" fill="#e91e63">
              Reducere
            </SvgText>

            <SvgText x="280" y="340" textAnchor="middle" fontSize="10" fontWeight="bold" fill="#2196f3">
              2H₂O → O₂(g) + 4H⁺ + 4e⁻
            </SvgText>
            <SvgText x="280" y="355" textAnchor="middle" fontSize="10" fill="#2196f3">
              Oxidare
            </SvgText>
          </Svg>
        </View>
      </View>
      {showGuide && <TheoreticalGuide onClose={() => setShowGuide(false)} />}
    </SafeAreaView>
  )
}

